.home {
    
    &__info {

        &__background {
            
            &__user {
                height: 100%;
                width: 50%;
                background-image: url(../../assets/Form-Hero-Image.jpg);
                background-size: cover;
                background-repeat: no-repeat;
                background-position-x: 60%;
            }
        }

        &__content {
            &__user {
                height: 100%;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;

                h1 {
                    font-size: 2.375rem;
                    color: var(--color-text);
                    font-family: var(--font-base);
                    text-align: center;
                }

                h2 {
                    margin-top: 77px;
                    font-size: 2.5rem;
                    color: var(--color-text);
                    font-family: var(--font-base);
                }

                &__steps {
                    display: flex;

                    &__columns {
                        margin-top: 3.75rem;
                        position: relative;
                        width: 25%;
                        color: var(--color-text);
                        font-family: var(--font-base);
                        text-align: center;

                        h1 {
                            font-size: 2rem;
                        }

                        span {
                            font-size: 1.5rem;
                        }


                        &:after {
                            position: absolute;
                            display: block;
                            content: "";
                            width: 150px;
                            height: 150px;
                            border: 1px solid black;
                            
                            transform: rotate(45deg);
                            top: -30%
                        }
                    }

                    
                }
            }
        }


    }
}