.login {
    display: flex;
    flex-direction: column;
    padding-top: 7.5rem;


    h1 {
        font-size: 2.5rem;
        color: var(--color-text);
        font-family: var(--font-base);
        text-align: center;

        &:after {
            content: "";
            display: block;
            position: absolute;
            width: 16rem;
            height: 2rem;
            background-image: url("../../assets/Decoration.svg");
            background-position: center;
            background-repeat: no-repeat;
            background-size: cover;
            left: calc(50% - 8rem);
            top: 32%
          }
    }

    form {
        display: flex;
        flex-direction: column;

    
    }

    &__inputs {
        display: flex;
        flex-direction: column;
        padding: 2.5rem 4rem 2.5rem 4rem;
        font-size: 1rem;
        font-family: var(--font-base);
        font-weight: bold;
        color: var(--color-text);
        background-color: var(--color-background);
        margin: 7.375rem 46rem 0 46rem;

        input {
            background-color: transparent;
            border: none;
            border-bottom: 1px solid var(--color-secondary);
            outline: none;
            margin-top: 1.3rem;
            margin-bottom: 1.5em
        }
    
    }

    &__btns {
        display: flex;
        justify-content: space-between;
        margin: 3.3125rem 44rem 0 44rem;
        font-size: 1.2rem;
    }
}